import { useState, useEffect } from "react";
import { getUsers } from "../services/mockapi"
import CardList from "../components/CardList/CardList";
import { statusFilters } from "../components/CardFilter/CardFilter";

const Tweets = () => {
  const [users, setUsers] = useState([]);

  useEffect(() => {
    getUsers()
      .then((response) => {
        setUsers(response.data);
      })
      .catch((error) => {
        return error;
      });
  }, []);

  // const getFilteredUsers = (users, statusFilter) => {
  //   switch (statusFilter) {
  //     case statusFilters.notFollowed:
  //       return users.filter((user) => user.isFollowed === false);
  //     case statusFilters.followed:
  //       return users.filter((user) => user.isFollowed === true);
  //     default:
  //       return users;
  //   }
  // };

  // const visibleUsers = getFilteredUsers(users, filter);

  return (
    <>   
        <CardList users={users} />
    </>
  );
};

export default Tweets;