import { useState, useRef } from "react";
import { useLocation } from "react-router-dom";
import PropTypes from "prop-types";
import Card from "../Card/Card";
import BtnLoadMore from "../Btn/BtnLoadMore";
import { GoBackBtn } from "../Btn/GoBackBtn";
import { FilterList } from "../CardFilter/CardFilter";
import { List, Container, Wrapper } from "./CardList.styled";

const CardList = ({ users }) => {
  const cardsPerPage = 3;
  const [next, setNext] = useState(cardsPerPage);
  const handleMoreCards = () => {
    setNext(next + cardsPerPage);
  };

  const location = useLocation();
  const backLinkLocationRef = location.state?.from ?? "/home";
  const previousLocation = useRef(backLinkLocationRef);

  const FilterStatus = {
    all: "all",
    followed: "followed",
    notFollowed: "notFollowed",
  };
  const FilterStatusOptions = [
    { label: "ALL", value: FilterStatus.all },
    { label: "FOLLOWED", value: FilterStatus.followed },
    { label: "NOT FOLLOWED", value: FilterStatus.notFollowed },
  ];
  const handleChangeOnFilter = (value) => {
    setFilter(value);
  };
  const [filter, setFilter] = useState(FilterStatus.all);

  return (
    <Container>
      <Wrapper>
        <GoBackBtn previousLocation={previousLocation} />
        <FilterList
          options={FilterStatusOptions}
          value={filter}
          onSelect={handleChangeOnFilter}
        />
      </Wrapper>
      <List>
        {users
          ?.slice(0, next)
          ?.map(({ id, tweets, followers, avatar, user }) => (
            <Card
              key={id}
              id={id}
              tweets={tweets}
              followers={followers}
              avatar={avatar}
              user={user}
            />
          ))}
      </List>
      {next < users?.length && (
        <BtnLoadMore onClick={handleMoreCards}>Load more</BtnLoadMore>
      )}
    </Container>
  );
};

CardList.propTypes = {
  users: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      user: PropTypes.string,
      avatar: PropTypes.string,
      tweets: PropTypes.number,
      followers: PropTypes.number,
    })
  ),
};

export default CardList;
